body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header__logo {
  width: 10rem;
  margin: 1.5rem auto;
}
@media (min-width: 768px) {
  .header__logo {
    width: 13rem;
    margin: 1.5rem auto 2rem;
  }
}

.wrap-text {
  width: 80%;
  position: absolute;
  top: 10%;
  left: 10%;
  color: white;
}
.home-title {
  font-size: 1.6rem;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  font-family: 'Josefin Sans', sans-serif;
}
@media (min-width: 768px) {
  .wrap-text {
    width: 50%;
    top: 15%;
  }
}
.dropdown-placeholder {
  position: absolute;
  bottom: -10%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  background: white;
  width: 80%;
  height: 20rem;
  border-radius: 6rem;
  box-shadow: 2px 2px 11px 0px #888888;
  display: flex;
  justify-content: center;
  align-items: center;
}
.filter {
  width: 80%;
  display: flex;
  flex-direction: column;
}
select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
.options {
  background: #f43945;
  color: white;
  font-family: 'Josefin Sans';
  font-size: 1rem;
  padding: 1rem;
  margin: .5px;
  border-radius: 0;
  border: none;
  outline: none;
}
select {
  flex-wrap: wrap;
  margin: auto;
}

@media (min-width: 768px) {
  .dropdown-placeholder {
    top: 65%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 8rem;
    border-radius: 3rem;
    box-shadow: none;
  }
  .filter {
    flex-direction: row;
  }
  .options {
    flex: 1 1;
  }
  select {
    background:none\9;
    padding: 5px\9;
}
}

.wrap-hero {
  height: 740px;
}
.wrap-img-text-drop {
  height: 80%;
  position: relative;
}
.wrap-image {
  height: 100%;
}
@media (min-width: 768px) {
  .wrap-hero {
    height: 75vh;
  }
}

.background {
  width: 85%;
  margin: 0 auto 6rem;
}
.flex-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 0.8rem;
  grid-row-gap: 0.7rem;
  padding-bottom: 40px;
}
.wrap-recipe {
  position: relative;
}
.wrap-text-on-img {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 0.4em;
  color: white;
  text-transform: uppercase;
  background: rgba(45, 6, 6, 0.6);
  cursor: pointer;
}
.country-name {
  border-bottom: 0.1px solid white;
  padding: 0.5em;
  margin: 0.5em;
}
.recipe-title {
  font-size: 1.5em;
}
@media (min-width: 700px) {
  .background {
    width: 70%;
  }
  .flex-grid {
    grid-template-columns: 30.5% 30.5% 30.5%;
    grid-column-gap: 2rem;
    grid-row-gap: 2rem;
  }
  .wrap-text-on-img {
    font-size: 0.8em;
    opacity: 0;
    transition: all 0.5s;
    background: rgba(45, 6, 6, 0.8);
  }
  .recipe-title {
    font-size: 1em;
  }
  .country-name {
    border-bottom: 0.5px solid white;
    font-size: .8rem;
  }
  .wrap-recipe:hover .wrap-text-on-img {
    opacity: 1;
  }
}

.recipe-wrap-image {
  background: #f43945;
  width: 100%;
  height: 50vh;
}
.wrap-country-time {
  width: 80%;
  margin: 2rem auto 3rem;
  display: flex;
  justify-content: space-between;
}
.wraw-details-title {
  width: 80%;
  margin: auto;
}
.details-title {
  font-size: 3rem;
  margin: 0 0 3rem;
  padding: 0 0 2rem;
  border-bottom: 1px solid #f43945;
}
.details-subtitle {
  margin: 4rem 0;
  font-size: 2rem;
}
.details-content-text, .details-content-recipe, .details-content-instructions {
  margin: 3rem 0 0 0 ;
}
.details-content-instructions {
  width: 100%;
  margin: 3rem 0 6rem 0;
}
li {
  list-style-type: none;
  margin: 1rem 0;
}

.details-content-recipe li::before {
  content: "\2022";
  color: red;
  margin-right: 1rem;
}
@media (min-width: 768px) {
  .recipe-image {
    width: 60%;
    margin: auto;
  }
  .wrap-country-time {
    width: 60%;
  }
  .wraw-details-title {
    width: 60%;
  }
  .details-content-instructions {
    width: 70%;
  }
}

.footer {
  background: #830e1f;
  color: white;
  text-align: center;
  font-size: 0.8rem;
  padding: 2.5em 0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: 'Open Sans', sans-serif;
}
main{
  flex: 1 1;
}

